
export const getWindDirection = (deg: number): string => {
    switch (true) {
        case (deg === 0 || deg === 360): { return '' }
        case (deg > 0 && deg < 90): { return '-' }
        case (deg === 90): { return '' }
        case (deg > 90 && deg < 180): { return '-' }
        case (deg === 180): { return '' }
        case (deg > 180 && deg < 270): { return '-' }
        case (deg === 270): { return '' }
        case (deg > 270 && deg < 360): { return '-' }
        default: return ''
    }
}